import React from 'react';
import injectSheet from 'react-jss';
import Img from 'gatsby-image';
import { graphql, StaticQueryDocument } from 'gatsby';

import Layout from '../components/layout';
import BackgroundImageSection from '../components/BackgroundImageSection';
import Section from '../components/Section';
import Link from '../components/Link';
import ImageSnippet from '../components/ImageSnippet';
import Divider from '../components/Divider';

const maxWidth = '73.85rem';

const styles = {
    buttonRect: {
        width: '21em',
        height: '3.67em',
        border: 'none',
        backgroundColor: '#43AAFF'
    },
    section: {
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        fontSize: '1.06rem',
        '& h4': {
            textAlign: 'center'
        },
        '& p': {
            width: '78%',
            textAlign: 'center',
            lineHeight: 1.3
        }
    },
    diagram: {
        width: '100%',
        margin: '5rem 0 10rem 0'
    },
    sectionFeatures: {
        display: 'flex',
        flexWrap: 'wrap',
        justifyContent: 'center'
    },
    contacts: {
        display: 'flex',
        flexWrqp: 'wrap',
        justifyContent: 'center',
        margin: '2rem 0'
    },
    howworks: {
        width: '100%'
    }
};

export default injectSheet(styles)(({ classes, data }) => {
    const sectionTop = (
        <BackgroundImageSection
            taller={true}
            maxWidth={maxWidth}
            sizesBackground={data.imageBackground.childImageSharp.fluid}
            justifyContent='center'>
            <h1>{data.contents.home.title}</h1>
            <p>{data.contents.home.description}</p>
            <Link to="/downloads">
                <button className={classes.buttonRect} type="button">
                    {data.contents.home.download}
                </button>
            </Link>
        </BackgroundImageSection>
    );
    
    const features = data.contents.home.feature.map((feature, index) => {
        let sizes;
        data.imageFeatures.edges.some((edge, index) => {
            if (edge.node.name === feature.name) {
                sizes = edge.node.childImageSharp.fluid;
                return true;
            }
            return false;
        });

        return sizes ? (
            <ImageSnippet key={index}>
                <Img style={{ width: '6.47rem' }} fluid={sizes} />
                <h1>{feature.header}</h1>
                <p>{feature.description}</p>
            </ImageSnippet>
        ) : null;
    });

    const sectionFeatures = (
        <Section className={classes.sectionFeatures} maxWidth={maxWidth}>
            {features}
        </Section>
    );

    const sectionHowWorks = (
        <Section className={classes.section} maxWidth={maxWidth}>
            <h4>{data.contents.home.howworks.header}</h4>
            <p>{data.contents.home.howworks.description}</p>
            <Img
                outerWrapperClassName={classes.diagram}
                className={classes.howworks}
                fluid={data.imageHowWorks.childImageSharp.fluid}
                />
        </Section>
    );

    const sectionMoving = (
        <BackgroundImageSection
            taller={true}
            maxWidth={maxWidth}
            sizesBackground={data.imageMoving.childImageSharp.fluid}
            justifyContent='center'
            boxShadow='none'>
            <h1>{data.contents.home.moving.header}</h1>
            <p>{data.contents.home.moving.description}</p>
            <Link to="/usecase">
                <button className={classes.buttonRect} type="button">
                    {data.contents.home.moving.usecase}
                </button>
            </Link>
        </BackgroundImageSection>
    );

    const contacts = [{
        name: "contact@onairvr.io",
        to: "mailto:contact@onairvr.io",
        sizes: data.imageContactMail.childImageSharp.fluid
    }, {
        name: "www.clicked.co.kr",
        to: "http://clicked.co.kr",
        sizes: data.imageContactSite.childImageSharp.fluid
    }].map((contact, index) => {
        return (
            <ImageSnippet key={index} width='12.4rem' height='12rem'>
                <Link to={contact.to}>
                    <Img fluid={contact.sizes} />
                    <p>{contact.name}</p>
                </Link>
            </ImageSnippet>
        );
    });

    const sectionContact = (
        <Section className={classes.section} maxWidth={maxWidth}>
            <h4>{data.contents.home.contact.header}</h4>
            <p>{data.contents.home.contact.description}</p>
            <div className={classes.contacts}>
                {contacts}
            </div>
        </Section>
    );
    
    return (
        <Layout>
            <div>
                {sectionTop}
                {sectionFeatures}
                <Divider maxWidth={maxWidth} />
                {sectionHowWorks}
                {sectionMoving}
                {sectionContact}
            </div>
        </Layout>
    );
});

export const query = graphql`
    query IndexHomeQuery {
        ...IndexHomeFragment
    }

    fragment IndexHomeFragment on Query {
        imageBackground: file(relativePath: { eq: "bg_home.jpg" }) {
            childImageSharp {
                fluid(maxWidth: 2000, quality: 90) {
                    ...GatsbyImageSharpFluid
                }
            }
        }

        imageFeatures: allFile(filter: {relativeDirectory: { eq: "features" } }) {
            edges {
                node {
                    name
                    childImageSharp {
                        fluid(maxWidth: 110) {
                            ...GatsbyImageSharpFluid
                        }
                    }
                }
            }
        }

        imageHowWorks: file(relativePath: { eq: "howworks.png" }) {
            childImageSharp {
                fluid(maxWidth: 2000) {
                    ...GatsbyImageSharpFluid
                }
            }
        }

        imageMoving: file(relativePath: { eq: "bg_moving.jpg" }) {
            childImageSharp {
                fluid(maxWidth:2000) {
                    ...GatsbyImageSharpFluid
                }
            }
        }

        imageContactMail: file(relativePath: { eq: "contact_mail.png" }) {
            childImageSharp {
                fluid(maxWidth: 150) {
                    ...GatsbyImageSharpFluid
                }
            }
        }

        imageContactSite: file(relativePath: { eq: "contact_site.png" }) {
            childImageSharp {
                fluid(maxWidth: 150) {
                    ...GatsbyImageSharpFluid
                }
            }
        }

        contents: stringsToml {
            home {
                title
                description
                download
                feature {
                    name
                    header
                    description
                }
                howworks {
                    header
                    description
                }
                moving {
                    header
                    description
                    usecase
                }
                contact {
                    header
                    description
                }
            }
        }
    }
`;
